      border-left: 2px solid white;
      animation: animate 6s steps(18) infinite,
      cursor .5s infinite step-end alternate;
      overflow: hidden;
      
     
  }

  .end-text:before{
   content: "";
   position: absolute;
   top: 0;
   left: 0;
   height: 100%;
   width: 200px;
