.sec-text:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 200px;
  background-color: #1e1e1e;
  border-left: 2px solid white;
  animation: animate 6s steps(18) infinite, cursor 0.5s infinite step-end alternate;
  overflow: hidden;
}

.end-text:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 200px;
  background-color: #1e1e1e;
  border-left: 2px solid white;
  animation: animate 6s steps(18) infinite, cursor 0.5s infinite step-end alternate;
  overflow: hidden;
}

@keyframes animate {
  40%, 60% {
    left: calc(100% + 4px);
  }
  100% {
    left: 0;
  }
}
@keyframes cursor {
  50% {
    border-color: transparent;
  }
}
